import ExpandLessRoundedIcon from "@mui/icons-material/ExpandLessRounded";
import ExpandMoreRoundedIcon from "@mui/icons-material/ExpandMoreRounded";
import ForwardToInboxRoundedIcon from "@mui/icons-material/ForwardToInboxRounded";
import NoAccountsIcon from "@mui/icons-material/NoAccounts";
import PersonIcon from "@mui/icons-material/Person";
import {
    Avatar, Collapse, Grid, IconButton, Paper,
    Stack,
    Tooltip
} from "@mui/material";
import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";
import Typography from "@mui/material/Typography";
import { styled } from "@mui/system";
import * as React from "react";
import { useParams } from "react-router";
import { StatusContext } from "../../../App";
import man from "../../../assets/pics/man.webp";
import shape from "../../../assets/pics/shape.svg";
import { HandleStatus } from "./UsersTable";
const Cover = styled(Box)(({ theme, bg }) => ({
  width: "100%",
  height: "33%",
  background: `center / cover no-repeat  url(${bg}) `,
  borderRadius: "15px 15px 0 0",
  marginBottom: "2.8rem",
}));

export default function UsersCard({ openData, Data }) {
  const [open, setopen] = React.useState(false);
  const [SingleUser, setSingleUser] = React.useState(undefined);
  let { user_index } = useParams();
  const { dispatch } = React.useContext(StatusContext);
  React.useEffect(() => {
    if (Data[user_index]) {
      const res = Data.filter((user, index) => index == user_index);
      setSingleUser({ ...res[0] });
    } else {
      openData.handleClose();
      dispatch({ type: "showError", payload: "No user with This ID" });
    }
  }, [user_index]);

  const ref = React.useRef({});
  const ToggleOpen = () => {
    setopen(!open);
    console.log(SingleUser);
  };
  const Scroll = () => {
    ref.current.scroll({
      top: ref.current.scrollHeight + 100,
      behavior: "smooth",
    });
  };

  return (
    <div>
      <Modal
        open={openData.open}
        onClose={openData.handleClose}
        sx={{ backdropFilter: "blur(2px)" }}
      >
        <Paper sx={classes.root} ref={ref}>
          {SingleUser ? (
            <>
              <Stack
                sx={classes.relative}
                direction="column"
                alignItems="center"
              >
                <span style={classes.cover} />
                <Cover bg={man} />
                <img src={shape} style={classes.shape} />
                <Avatar src={man} sx={classes.avatar} />
                <Typography sx={classes.name}>{SingleUser.name}</Typography>
                <OptionStack />
                <HandleStatus status={SingleUser.status} />
                <InfoStack SingleUser={SingleUser} />
                <IconButton
                  sx={{ width: 4, position: "absolute", bottom: 0 }}
                  disableRipple
                  onClick={ToggleOpen}
                >
                  {open ? <ExpandLessRoundedIcon /> : <ExpandMoreRoundedIcon />}
                </IconButton>
              </Stack>
              <MoreInfo open={open} Scroll={Scroll} SingleUser={SingleUser} />
            </>
          ) : (
            <>Nouser</>
          )}
        </Paper>
      </Modal>
    </div>
  );
}

const MoreInfo = ({ open, Scroll, SingleUser }) => {
  return (
    <Box sx={{ px: 2 }}>
      <Collapse in={open} timeout={0} addEndListener={Scroll}>
        <Stack direction="column" spacing={2}>
          <Stack direction="column" spacing={0.5}>
            <Typography sx={classes.title}>Fullname :</Typography>
            <Typography sx={classes.value}>{SingleUser.name}</Typography>
          </Stack>
          <Stack direction="column" spacing={0.5}>
            <Typography sx={classes.title}>Phone :</Typography>
            <Typography sx={classes.value}>{SingleUser.phone}</Typography>
          </Stack>
          <Stack direction="column" spacing={0.5}>
            <Typography sx={classes.title}>Email :</Typography>
            <Typography sx={classes.value}>{SingleUser.email}</Typography>
          </Stack>
          <Stack direction="column" spacing={0.5}>
            <Typography sx={classes.title}>Adresse :</Typography>
            <Typography sx={classes.value}>{SingleUser.adresse}</Typography>
          </Stack>
        </Stack>
      </Collapse>
    </Box>
  );
};
const InfoStack = ({ SingleUser }) => {
  return (
    <Grid container sx={classes.inforoot}>
      <Grid item xs={4}>
        <Typography sx={classes.infoTitle}>Toure</Typography>
        <Typography sx={classes.infoValue}>{SingleUser.toure}</Typography>
      </Grid>
      <Grid item xs={4}>
        <Typography sx={classes.infoTitle}>On the Way</Typography>
        <Typography sx={classes.infoValue}>2</Typography>
      </Grid>
      <Grid item xs={4}>
        <Typography sx={classes.infoTitle}>Delivred</Typography>
        <Typography sx={classes.infoValue}>6</Typography>
      </Grid>
    </Grid>
  );
};

const OptionStack = () => {
  return (
    <Stack direction="row" spacing={1} sx={{ mb: 1 }}>
      <Tooltip title="Chat">
        <IconButton>
          <ForwardToInboxRoundedIcon />
        </IconButton>
      </Tooltip>
      <Tooltip title="Unblock">
        <IconButton color="success">
          <PersonIcon />
        </IconButton>
      </Tooltip>
      <Tooltip title="Block">
        <IconButton color="primary">
          <NoAccountsIcon />
        </IconButton>
      </Tooltip>
    </Stack>
  );
};

