function createData(avatar, name, items, Ddate) {
  return {
    avatar,
    name,
    items,
    Ddate,
  };
}

function createUsers(
  avatar,
  name,
  items,
  status,
  toure,
  phone,
  email,
  adresse
) {
  return {
    avatar,
    name,
    items,
    status,
    toure,
    phone,
    email,
    adresse
  };
}

